.card {
  margin-bottom: 15px;
  padding: 20px;
  width: 600px;
  font-size: 14px;
  background: #f3f3f3;
  border-radius: 8px;
  position: relative;

  &__info span:first-child {
    color: #a3a3a3;
    margin-right: 7px;
  }

  &__info:not(:last-child) {
    margin-bottom: 10px;
  }
}
